import React from 'react';

const HomePage = () => (
    <>
       <h1>Hello, This is my first blog using React!</h1>
       <p>
       We will build a small game during this tutorial. You might be tempted to skip 
       it because you’re not building games — but give it a chance. 
       The techniques you’ll learn in the tutorial are fundamental to
        building any React app, and mastering it will give you a deep understanding of React.
       </p>

       <p>
       We’ll assume that you have some familiarity with HTML and JavaScript,
        but you should be able to follow along even if you’re coming from a
         different programming language. We’ll also assume that you’re 
       familiar with programming concepts like functions, objects, arrays,
        and to a lesser extent, classes
       </p>
    </>
) ;

export default HomePage;